* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  position: relative;
}

.form-container {
  height: 60px;
  width: 100%;
  background-color: #00f;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.svg-icon {
  width: 15px;
  height: 15px;
}

.search-form {
  flex-direction: row;
  display: flex;
  position: relative;
}

.search-form > input {
  height: 25px;
  min-width: 230px;
  border: none;
  border-radius: 1px;
  outline: none;
}

.search-form > input:focus-within {
  outline: 3px solid #add8e6;
}

.search-btn {
  height: 25px;
  border: none;
  border-radius: 1px;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  right: 0;
}

